<!-- components/OpacityAnimation.vue -->

<template>
  <td
    v-if="isTableCell"
    class="animated-opacity"
    :class="[skeletonBackgroundColor, borderRadiusClass]"
  />
  <div
    v-else
    class="animated-opacity"
    :class="[skeletonBackgroundColor, borderRadiusClass]"
  />
</template>

<script>
export default {
  props: {
    shape: {
      type: String,
      default: "rectangle",
      validator: (value) => ["rectangle", "square", "circle"].includes(value),
    },
    skeletonBackgroundColor: {
      type: String,
      default: "bg-bg-neutral-raised",
    },
    borderRadius: {
      type: String,
      default: "",
    },
    isTableCell: {
      type: Boolean,
      default: false,
      required: false,
    },
  },
  computed: {
    borderRadiusClass() {
      let output = "rounded-04";
      if (this.shape === "circle") {
        output = "rounded-full";
      } else if (this.borderRadius) {
        output = this.borderRadius;
      } else if (this.shape === "square") {
        output = "rounded-08";
      }
      return output;
    },
  },
};
</script>

<style scoped>
.animated-opacity {
  animation: opacityAnimation 1.5s infinite alternate;
}

@keyframes opacityAnimation {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}
</style>
